.navbar {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    height: 7vh;
    padding: 15px;
    background-color: white;
    width: 100%;
    position: sticky;
    top: 0;
}

.logo {
    width: 125px;
    height: 125px;
    background-color: white;
    position: absolute;
    top: 15px;
    left: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    box-shadow: 1px 1px 0.5px #cccccc;
}

.logo img {
    width: 85%;
}

.links {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
}

.links li {
    margin-right: 10px;
}

@media screen and (min-width: 668px) {
    .navbar {
        height: 10vh;
    }
}