body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial;
}

* {
  box-sizing: border-box;
}

h2 {
  font-size: 88px;
  margin: 0;
  padding: 0;
}

h3 {
  font-size: 42px;
  background-color: black;
  padding: 5px 10px;
}

p {
  font-size: 21px;
  line-height: 26px;
  letter-spacing: -0.6px;
}

@media screen and (min-width: 668px) {

  h2 {
    font-size: 120px;
    margin: 0;
    padding: 0;
  }

  .h3 {
    font-size: 62px;
  }
}